<template>
    <div class="info">
        <h1>技术栈</h1>
        <transition
                :duration="{ enter: 2500, leave: 1500 }"
                leave-active-class="animated fadeOutRight"
                enter-active-class="animated fadeInLeft">
            <div class="cont" v-if="show">
                <template v-for="skill in skills">
                    <el-row :gutter="20" :key="skill.name">
                        <el-col :span="8">
                            <div class="left" :style="{backgroundColor: `${skill.background}`}">
                                <h2> {{skill.name}}</h2>
                            </div>
                        </el-col>
                        <el-col :span="16">
                            <el-row :gutter="10">
                                <template v-for="child in skill.child">
                                    <el-col :span="12" :key="child.name">
                                        <div class="right" :style="{backgroundColor: child.background}">
                                            <h3>{{child.name}}</h3>
                                        </div>
                                    </el-col>
                                </template>
                            </el-row>
                        </el-col>
                    </el-row>
                </template>
            </div>
        </transition>
    </div>
</template>
<script>
    export default {
        // let randomcolor = rgba()
        data() {
            return {
                show: false,
                skills: [
                    {
                        name: '服务端',
                        background: 'rgba(29, 218, 61, 0.2)',
                        child: [
                            {name: 'SpringBoot', background: 'rgba(29, 218, 61, 0.2)'},
                            {name: 'MySQL', background: 'rgba(29, 218, 61, 0.2)'},
                            {name: 'solr', background: 'rgba(29, 218, 61, 0.2)'},
                            {name: 'MyBatis', background: 'rgba(29, 218, 61, 0.2)'}
                        ]
                    },
                    {
                        name: '前端',
                        background: 'rgba(211, 184, 30, 0.2)',
                        child: [
                            {name: 'Vue', background: 'rgba(211, 184, 30, 0.2)'},
                            {name: 'NodeJs', background: 'rgba(211, 184, 30, 0.2)'},
                            {name: 'layui', background: 'rgba(211, 184, 30, 0.2)'},
                            {name: 'uniapp', background: 'rgba(211, 184, 30, 0.2)'}
                        ]
                    },
                    {
                        name: '工具',
                        background: 'rgba(232, 13, 24, 0.2)',
                        child: [
                            {name: 'Git', background: 'rgba(232, 13, 24, 0.2)'},
                            {name: 'Centos', background: 'rgba(232, 13, 24, 0.2)'},
                            {name: '禅道', background: 'rgba(232, 13, 24, 0.2)'},
                            {name: '阿里云', background: 'rgba(232, 13, 24, 0.2)'}
                        ]
                    }
                ]
            }
        },
        methods: {
            timeout() {
                setTimeout(() => {
                    this.show = true
                }, 1000)
            }
        },
        mounted() {
            this.timeout()
        }
    }
</script>

<style lang="scss" scoped>
    .info {
        @extend .info;
        margin: 30px $spacing-row-base;

        .cont {
            @extend .cont;

            .left {
                margin: 10px auto;
                width: 100%;
                height: 160px;
                // background-color: rgba(211, 184, 30, 0.2);
                line-height: 160px;

                h2 {
                    margin-left: $spacing-row-base;
                }
            }

            .right {
                height: 70px;
                // background-color: rgba(232, 13, 24, 0.2);
                line-height: 70px;

                h3 {
                    margin-left: $spacing-row-base;
                }
            }
        }
    }
</style>
